import React, { Component } from "react";
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { Col } from 'react-simple-flex-grid';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from "@material-ui/icons/SearchRounded";
import "react-simple-flex-grid/lib/main.css";
import logo from "./logo.png";
import "./App.css";

const theme = createMuiTheme({
  typography: {
    fontFamily: [
      'Montserrat',
      'sans-serif'
    ].join(','),
  }
});

const useStyles = theme => ({
  root: {
    flexGrow: 1,
  },

  searchBox: {
    padding: '3px 45px',
    display: 'flex',
    alignItems: 'center',
    width: "auto",
    borderRadius: "30px",
  },

  input: {
    marginLeft: theme.spacing(5),
    width: "200px",
    flex: 1,
  },

  iconButton: {
    background: "grey",
    color:"#E7E7E7",
    padding: 10,
  },

  cardContainer: {
    overflow:"auto",
    height: "55vh",
    width: "auto",
    display: 'block',
    minWidth: '280px',
    position: 'center',
    justifyContent: 'space-evenly',
    boxSizing: 'border-box',
    background: "linear-gradient(to top, #304352, #d7d2cc)",
    borderRadius: "20px",
    boxShadow: "0px 15px 20px 0px rgba(0,0,0,0.75)",
    color: "#E7E7E7",
    textAlign: "center",
    marginTop: "40px",
    cursor: "pointer",
    '&:hover': {
      transform: "scale(1.1)",
      cursor: "pointer",
    },
    '&:active': {
      background: "#630012",
    }
  },
  
  center: {
    position: "relative",
    top: "50%",
    transform: "translateY(-50%)",
  },

  round: {
    border: "1px solid grey",
    borderRadius: "43%",
    padding: "7px",
  },

  nameContainer: {
    margin: "10px 0",
  },
});


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      searchValue: '',
    };
  }

  componentDidMount() {
    fetch("https://reqres.in/api/users?page=1")
     .then(response => response.json())
     .then(json => {
       console.log(json);
       this.setState({ data: json.data });
    });
  }

  updateSearch(event) {
    const search = event.target.value.toLowerCase();
    let newData = this.state.data.filter(item=>
      (item.email.toLowerCase().includes(search))
    )

    this.setState({
      searchValue: search,
      data: newData
    })
  }

  render() {

    const { classes } = this.props;

    return (
      <React.Fragment>
        <Container>
          <Box display="flex" paddingTop={9}>
            <Box m="auto" alignItems="center">
              <Paper component="form" className={classes.searchBox}>
                <img src={logo} alt="" width="40%" height="40%"/>
                <InputBase
                  className={classes.input}
                  placeholder="Search by Email"
                  value={this.state.value} onChange={this.updateSearch.bind(this)}
                />
                <IconButton type="submit" className={classes.iconButton} aria-label="search">
                  <SearchIcon />
                </IconButton>
              </Paper>
            </Box>
          </Box>
        </Container>
        {this.state.data.map(item => {
          return (
            <ThemeProvider theme={theme}>
              <Container className={classes.root} alignItems="center" justifyContent="center">
                <Col span={3} xs={12} sm={6} md={4} lg={3} xl={4}>
                  <Grid container spacing={0} flexDirection="column" alignItems="center" justify="center" style={{ minHeight: '73vh' }}>
                    <Card className={classes.cardContainer}>
                      <CardContent className={classes.center}>
                        <Typography>
                          <img className={classes.round} alt="" title={item.first_name} src={item.avatar}/>
                        </Typography>
                        <Typography className={classes.nameContainer}>
                          <span>{item.first_name}</span>
                          <span style={{paddingLeft:"6px"}}>{item.last_name}</span>
                        </Typography>
                        <Typography>
                          <span>{item.email}</span>
                        </Typography>
                      </CardContent>
                    </Card>
                  </Grid>
                </Col>
              </Container>
            </ThemeProvider>
          );
        })}
      </React.Fragment>
    );
  }
}

export default withStyles(useStyles)(App);
